.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.527); 
    display: flex;
    justify-content: center;
    align-items: center; 
}
  
  .popup {
    background-color: #222; 
    padding: 20px;
    border-radius: 10px;
    max-width: 40%;
    box-shadow: 0 0 20px rgb(255, 0, 0); 
  }
  
  .popup h2 {
    font-size: 1.8rem;
    margin-bottom: 20px;
    color: #fff; 
    box-shadow: 0 0 5px aqua;
    border-radius: 10px;
    padding: 10px;
    text-align: center;
  }
  
  .popup p {
    font-size: 1.3rem;
    line-height: 1.5; 
    margin-top: 40px;
    margin-bottom: 30px;
    color: #aaa; 
  }
  
  .closeButton {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 10px;
    background-color: #333; 
    color: #fff; 
    border: none;
    border-radius: 5%;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .closeButton:hover {
    background-color: #555; 
  }
  
  /* Responsive */
  @media screen and (max-width: 1200px) {
    .popup {
      max-width: 80%;
      max-height: 85%;
      padding: 15px;
    }
  
    .popup h2 {
      font-size: 1.5rem; 
    }
  
    .popup p {
      font-size: 1rem; 
      line-height: 1.7; 
    }
  
    .closeButton {
      top: 5px; 
      right: 5px; 
      padding: 8px; 
    }
  }
  