}

.gridItem {
justify-content: center; 
align-items: center; 
width: 100%;
margin-top: 20px;
}

.gridItem img {
margin: auto;
height: 250px;
border-radius: 5px;
}

/*Item Container Poster/Titel/Beschreibung*/
.gridItemContent {
width: 200px;
background-color: rgba(85, 85, 85, 0.3);
margin-top: 20px;
display: flex; 
align-items: center; 
border-radius: 5px;
border: 0.5px solid #fcad02;
flex-direction: column;
}

.gridContainer a {
  color: inherit !important; /* Behält die Standardfarbe des Textes bei */
  text-decoration: none !important; /* Entfernt die Unterstreichung */
}



/*Kompletter Container Hintergrund*/
.gridContainer {
  margin: 0 auto; 
  width: 80%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px; 
  padding: 1%;
  justify-content: space-between;
}

.gridItemContent:hover {
  cursor: pointer;
  transform: scale(1.05); 
  transition: transform 0.3s ease;
  box-shadow: rgba(254, 178, 2, 0.75) 0px 0 10px;
}

/* Responsive Anpassungen */
@media screen and (max-width: 768px) {
  .gridContainer {
    width: 90%;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    justify-content: center; /* Zentriert die Elemente horizontal */
    align-items: center; /* Zentriert die Elemente vertikal */
  }

  .gridItem img {
    height: 200px;
  }

  .gridItemContent {
    width: 150px;
  }
}





